.datagrid {
  box-sizing: border-box;
  border: 1px solid #e6e6e6;
  position: relative;
  display: flex;
  flex-direction: column;
}

.datagrid--scroll {
  max-height: 50vh;
}

.datagrid__header, .datagrid__footer {
  flex-shrink: 0;
}

.datagrid__header {
  position: sticky;
  top: 70px;
  z-index: 10;
}

@media (min-width: 768px) {
  .datagrid__header {
    top: 0;
  }
}

.navigation-bar--sticky ~ * .datagrid__header {
  top: 70px;
}

