.wysiwyg p {
  color: black;
  font-family: "RO Sans", Calibri, sans-serif;
  margin: 0;
  margin-bottom: 0.8125rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

@media (min-width: 1200px) {
  .wysiwyg p {
    font-size: 1.125rem;
  }
}

.wysiwyg a {
  color: #01689b;
  cursor: pointer;
  text-decoration: underline;
}

.wysiwyg a:hover {
  color: #01689b;
  text-decoration: none !important;
}

.wysiwyg ul, .wysiwyg ol {
  color: black;
  font-family: "RO Sans", Calibri, sans-serif;
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  margin-bottom: 0.8125rem;
}

@media (min-width: 1200px) {
  .wysiwyg ul, .wysiwyg ol {
    font-size: 1.125rem;
  }
}

.wysiwyg hr {
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6;
  margin: 0;
  margin-bottom: 0.8125rem;
}

.wysiwyg table {
  color: black;
  font-family: "RO Sans", Calibri, sans-serif;
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  margin: 0.8125rem 0;
  border-collapse: collapse;
  text-align: left;
}

@media (min-width: 1200px) {
  .wysiwyg table {
    font-size: 1.125rem;
  }
}

.wysiwyg table tr {
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6;
}

.wysiwyg table tr:last-child {
  box-sizing: border-box;
  border-bottom: 1px solid #e6e6e6;
}

.wysiwyg table th, .wysiwyg table td {
  padding: 0;
}
